* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {

  & .d-f {
    display: flex;
  }
  
  & .p-r {
    position: relative;
  }
  
  .p-a {
    position: absolute;
  }
  
  .text-align-justify {
    text-align: justify;
  }
  
  .grey {
    color: grey;
  }

  & main {
    flex-direction: column;
  }
  

}


:is(a, a:hover) {
  text-decoration: none;
}



.article-layout {
  margin: 0 auto;
  max-width: 800px;
  padding: 80px 20px;
}

header {
  background-color: black;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 1;

  & .n-element {
    padding: 12px 24px;
    text-decoration: none;
    color: white;
  }

  & a.more-dropdown {
    text-decoration: none;
    color: white;
  }

  & .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;

    & a {
      float: none;
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
      text-align: left;
    }
  }

  & ul {
    align-items: center;
  }

  & .n-element:hover {
    background-color: grey;
  }

  & > a.n-element:hover {
    background-color: red;
  }

  & .dropdown:hover .dropdown-content {
    display: block;
  }
}

section {
  text-align: center;

  & h2 {
    padding: 10px 0;
  }

  & p {
    padding: 15px 0;
    line-height: 1.2;
  }
}

.first-section {
  & img {
    width: 100%;
  }
  & div {
    color: white;
    top: 650px;
    width: 100%;

    & * {
      padding: 10px;
    }
  }
}

.second-section {
  & img {
    width: 140px;
    border-radius: 3px;
  }

  & .cards {
    display: flex;
    padding: 30px 0;

    & .card {
      flex-grow: 1;
    }
  }
}

.third-section {
  background-color: black;
  color: white;

  & ul {
    background-color: white;
    list-style: none;
    text-align: left;
    padding: 10px 0;

    & li {
      padding: 10px 16px;

      & .sold-out {
        background-color: red;
        color: white;
        padding: 3px 7px;
        margin-left: 10px;
      }

      & .count {
        background-color: black;
        color: white;
        padding: 1px 7px;
        border-radius: 15px;
        float: right;
      }
    }
  }
}
